@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
body {
  margin: 0;
  background-color: #111 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  scroll-behavior: smooth;
  text-align: center;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #111;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  text-decoration: none;
}

.banner {
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}


.navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 30px 30px;
}

.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    
}

.navItem {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #edf0f1;
    text-decoration: none;
    padding: 10px;
    transition: all 0.3s ease;
}

.navItem:hover {
    color: #0088a9;
}

.about, .contact, .projects {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}


.navLink {
    width: 150px;
    padding: 15px;
    text-decoration: none;
    border-radius: 8px 8px 8px 8px;
    font-weight: 500;
    font-size: 16px;
    color: #edf0f1;
    transition: all 0.3s ease;
}

.navLink:hover {
    color: #0088a9;
}
.bio {
    max-width: 600px;
    margin: 0 auto;
    line-height: 32px;
}

.external-link {
    cursor: pointer;
    border-bottom: .5rem honeydew;
}

.external-link:hover {
    -webkit-animation: pulse 3s infinite;
            animation: pulse 3s infinite;
}

@-webkit-keyframes pulse {
    0% {
        color: #FF8E53;
    }
    100% {
        color: #FE6B8B;
    }
}

@keyframes pulse {
    0% {
        color: #FF8E53;
    }
    100% {
        color: #FE6B8B;
    }
}


.headshot { 
    margin: 20px;
    max-width: 400px;
    border-radius: 15px;
    animation: fadeIn ease 3s;
    -webkit-animation: fadeIn ease 3s;
    -moz-animation: fadeIn ease 3s;
    -o-animation: fadeIn ease 3s;
    -ms-animation: fadeIn ease 3s;
    }
    @keyframes fadeIn {
    1% {opacity:0.1;}
    100% {opacity:1;}
    }
    
    @-webkit-keyframes fadeIn {
        1% {opacity:0.1;}
        100% {opacity:1;}
    }


.container {
    height: 100vh;
    padding: 0 20px;
    margin: 0 auto;
    max-width: 1344px;
}

.formColumn {
    padding: 10px;
    width: 55px;
}

.formColumn .input {
    width: 30rem;
}

.formColumn .messageInput {
    width: 30rem;
    height: 10rem;
}

.label {
    padding: 3px;
}

.card-container {
    /* max-width: 1100px; */
    margin-top: 30px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0;
    padding: 0;
}

